import http from '@/assets/http-commom.js'
import Swal from 'sweetalert2'

class PaymentService {

    load_invoice(pk){
        return http
            .get(`payment/${pk}/load_invoice/`)
            .then(res => {
                return res.data
            })
    }

    pay(data){
        return http
            .post('payment/pay/',{
                invoice_id: data.invoice_id,
                payment_object: data.payment_intent
            })
            .then(res => {
                return res.data
            })
    }

    load_payment(pk){
        return http
            .get(`payment/${pk}/payments/`)
            .then(res => {
                return res.data
            })
    }

    list_admin(){
        return http
            .get('payment/all/')
            .then(res => {
                return res.data
            })
    }

    list_customer(pk){
        return http
            .get(`payment/${pk}/payments/`)
            .then(res => {
                return res.data
            })
    }

    list_admin_reports(){
        return http
            .get('payment_report/all/')
            .then(res => {
                return res.data
            })
    }

    list_customer_reports(pk){
        return http
            .get(`payment_report/${pk}/reports/`)
            .then(res => {
                return res.data
            })
    }

    send_report_problem(data){
        return http
        .post('payment_report/', {
            payment: data.payment,
            issue_description: data.issue_description
        })
    }

    send_report_payment(data){
        return http
        .post('payment/', {
            amount: parseFloat(data.amount).toFixed(2),
            date: data.date,
            invoice: data.invoice,
            method: data.method,
            status: data.status,
            stripe_confirmation_number: data.reference,
        })
    }

    export_payments(data){
        return http
            .post('payment/export_payments/', {
                id_payments: data
            }, {responseType: 'blob'})
            .then(res => {
                return res
            })
    }

    export_payments_reports(data){
        return http
            .post('payment_report/export_reports/', {
                id_payments: data
            }, {responseType: 'blob'})
            .then(res => {
                return res
            })
    }
}

export default new PaymentService()