<template>
    <div>

<nav-bar :name="title"></nav-bar>

    <v-card class="ma-1" cols="12">
        <v-container>
                <v-row>
                    <v-col cols="12">
                        <v-select
                        class="mb-n5"
                        outlined
                        dense
                        :items="items_invoices"
                        v-model="invoice"
                        @change="invoice_select"
                        label="Invoice"
                        ></v-select>
                    </v-col>
                    <v-col cols="12">
                        <v-text-field
                        class="mb-n5"
                        outlined
                        dense
                        label="You're paying"
                        disabled
                        v-model="payment.invoice_amount"
                        ></v-text-field>
                    </v-col>

                    <v-col cols="12">
                        <div id="card-element">
                            <small>
                                Select the invoice you desire to pay!
                            </small>
                        </div>
                        <div id="card-errors" role="alert"></div>
                    </v-col>

                    <v-col>
                        <v-btn
                        class="mx-4 my-2"
                        color="#989898"
                        dark
            @click="$router.go(-1)"
                        >
                        GO BACK
                        </v-btn>
                        <v-btn
                        class="mx-4 my-2"
                        color="#E47911"
                        dark
                        :disabled="!payment.card"
                        :loading="loading"
                        @click="process_payment"
                        >
                        PAY NOW
                        </v-btn>
                    </v-col>
                </v-row>
        </v-container>
    </v-card>

    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import PaymentService from '@/services/PaymentService';
import InvoiceService from '@/services/InvoiceService';
import Swal from 'sweetalert2'

export default {
    components: {
        NavBar
    },

    data: () => ({
        title: 'MAKE A PAYMENT',
        items_invoices: [ ],
        invoice: '',
        loading: false,
        payment: {
            invoice_amount: '',
            invoice_id: '',
            client_secret: '',
            card: null,
        },
        key: null,
        execution_mode: false,
    }),

    computed: {
        idStore(){
            return this.$store.state.auth.store_user.unique_id;
        },

        stripe(){
            this.key = this.execution_mode ? "pk_test_Gc68KaHPuajzfgnCbcmGcbgC001gYPnK8C" : "pk_live_VXeFxTdzMvwmWR1IJYQuvWiX00lnSquolS";

            return Stripe(this.key);
        }
    },

    mounted() { 
        if(this.payment.card){
            this.payment.card.on('change', ({error}) => {
                let displayError = document.getElementById('card-errors');
                if (error) {
                    displayError.textContent = error.message;
                } else {
                    displayError.textContent = '';
                }
            });
        }      

        InvoiceService
        .list_pending(this.idStore)
        .then(res => {
            res.forEach(item => {
                this.items_invoices.push({ text: 'Invoice: ' + item.unique_id , value: item.unique_id})
            });
        })
        .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
                msg+= key + ': ' + value + ' \n ';
            });
                this.loading = false;
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: msg
                });
            })
    },

    methods:{
        invoice_select(){
            PaymentService
            .load_invoice(this.invoice)
            .then(res => {
                this.payment.invoice_amount = res.invoice_amount;
                this.payment.invoice_id = res.invoice_id;
                this.payment.client_secret = res.client_secret;


                let elements = this.stripe.elements({
                    fonts: [
                        {
                            cssSrc: 'https://fonts.googleapis.com/css?family=Roboto',
                        },
                    ],
                    // Stripe's examples are localized to specific languages, but if
                    // you wish to have Elements automatically detect your user's locale,
                    // use `locale: 'auto'` instead.
                    locale: window.__exampleLocale
                });
                
                
                let style = {
                    base: {
                        color: "#000",
                    }
                };
                let card = elements.create('card', { style: style });
                card.mount("#card-element");
                this.payment.card = card;
                
            })
            .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
                msg+= key + ': ' + value + ' \n ';
            });
                this.loading = false;
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: msg
                });
            })
        },

        process_payment(){
            this.loading = true;
            this.stripe.confirmCardPayment(this.payment.client_secret,
            {
                payment_method: {
                card: this.payment.card
                }
            }).then(result => {
                if (result.error) {
                    Swal.fire({
                        icon: 'error',
                        title: 'Error',
                        text: result.error.message
                    });
                } else {
                    // The payment has been processed!
                    if (result.paymentIntent.status === 'succeeded') {
                        this.payment.payment_intent = result.paymentIntent;
                     //        console.log('Payment successful confirmed in the status',Payment,result);
                        this.finish_payment_server();
                    }
                    else{
                        Swal.fire({
                            icon: 'info',
                            title: 'Info',
                            text: paymentIntent.status
                        });
                    }
                }
            })
            .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
                msg+= key + ': ' + value + ' \n ';
            });
                this.loading = false;
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: msg
                });
            })
        },

        finish_payment_server(){
            PaymentService
            .pay(this.payment)
            .then(res =>{
                this.loading = false;
                Swal.fire({
                    icon: 'success',
                    title: 'Success',
                    text: res['message'],
                }).then( () => {
                    this.$router.go(-1);
                })
            })
            .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
                msg+= key + ': ' + value + ' \n ';
            });
                this.loading = false;
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: msg
                });
            })
        }

    },
}
</script>