import http from '@/assets/http-commom.js'
import Swal from 'sweetalert2'

class UserService {
    list_admin(){
        return http
            .get('admin/users/')
            .then(res => {
                return res.data
            })
    }

    delete(pk){
        return http
            .delete('admin/users/'+pk+'/')
            .then(res => {
                return res.data
            })
    }

    register(data){
        let formData = new FormData();
        formData.append('email', data.email)
        formData.append('username', data.username)
        formData.append('img', data.img)
        formData.append('is_staff', data.staff)
        formData.append('phone_number', data.phone)
        formData.append('password', data.password)
        formData.append('confirm_password', data.confirm_password)
        formData.append('first_name', data.firstname)
        formData.append('last_name', data.lastname)

        return http
            .post('admin/users/',formData, { headers: { 'content-type': 'multipart/form-data' }})
            .then(res => {
                return res.data
            })
    }

    update(data, pk){
        let formData = new FormData();
        formData.append('email', data.email)
        formData.append('username', data.username)
        formData.append('is_staff', data.staff)

        if(data.img)
            formData.append('img', data.img)

        formData.append('phone_number', data.phone)
        formData.append('first_name', data.firstname)
        formData.append('last_name', data.lastname)


        return http
            .put('user/'+pk+'/update_user/', formData, { headers: { 'content-type': 'multipart/form-data' }})
            .then(res => {
                return res.data
            })
    }

    get(pk){
        return http
            .get('admin/users/'+pk+'/')
            .then(res => {
                return res.data
            })
    }

    get_profile(){
        return http
            .get('user/profile/')
            .then(res => {
                return res.data
            })
    }

    update_profile(data, pk){
        let formData = new FormData();
        formData.append('email', data.email)
        formData.append('username', data.username)
        formData.append('is_staff', false)

        if(data.img)
            formData.append('img', data.img)

        formData.append('phone_number', data.phone)
        formData.append('first_name', data.firstname)
        formData.append('last_name', data.lastname)

        return http
            .put('user/'+pk+'/update_user/', formData, { headers: { 'content-type': 'multipart/form-data' }})
            .then(res => {
                return res.data
            })
    }

}

export default new UserService();