import http from '@/assets/http-commom.js'
import Swal from 'sweetalert2'

class InvoiceService {

    list_admin(){
        return http
            .get('admin/invoices/')
            .then(res => {
                return res.data
            })
    }

    list_invoices(pk){
        return http
            .get(`invoice/${pk}/store/`)
            .then(res => {
                return res.data
            })
    }

    list_pending(pk){
        return http
            .get(`invoice/${pk}/pending/`)
            .then(res => {
                return res.data
            })
    }

    add_invoice(data){
        let formData = new FormData();
        formData.append('file', data.file)
        formData.append('due_date', data.due_date)
        formData.append('description', data.description)
    
        return http
            .post('admin/invoices/',formData, { headers: { 'content-type': 'multipart/form-data' }})
            .then(res => {
                return res.data
            })
    }

    add_extra(data){    
        return http
            .post('admin/invoices/create_invoices/', {
                amount: data.amount,
                due_date: data.due_date,
                description: data.description,
                id_stores: data.id_stores,
            })
            .then(res => {
                return res.data
            })
    }

    delete_invoices(data){
        return http
            .post('admin/invoices/remove_invoices/', {
                id_invoices: data
            })
            .then(res => {
                return res.data
            })
    }

    export_invoices(data){
        return http
            .post('admin/invoices/export_invoices/', {
                id_invoices: data
            }, {responseType: 'blob'})
            .then(res => {
                return res
            })
    }

    update(data, pk){
        return http
            .put('admin/invoices/'+pk+'/', {
                amount: data.amount,
                due_date: data.due_date,
                description: data.description,
                status: data.status,
                store: data.store,
            })
            .then(res => {
                return res.data
            })
    }

    get(pk){
        return http
        .get('admin/invoices/'+pk+'/')
        .then(res => {
            return res.data
        })
    }

    download_invoice(pk){
        return http
            .get('invoice/'+pk+'/download_invoice', {responseType: 'blob'})
            .then(res => {
                return res.data
            })
    }

}

export default new InvoiceService();